import React from 'react'

export default ({
  onStringevent,
  onVoidevent,
  onObjevent,
  onNumevent,
}: {
  onStringevent: (val: string) => void
  onNumevent: (val: number) => void
  onObjevent: (val: { value: string }) => void
  onVoidevent: () => void
}) => {
  return (
    <button
      onClick={() => {
        onStringevent('demo')
        onNumevent(5)
        onObjevent({ value: 'val' })
        onVoidevent()
      }}
    >
      Send event
    </button>
  )
}
