import React from 'react'
          import {createRoot} from 'react-dom'
import Component from './ReactDemo'
class ReactWc extends HTMLElement {
  render() {
    const rootEl = document.createElement('div')
    const args = {
      text: (()=>{const value = this.attributes.getNamedItem('text')?.value; try{return JSON.parse(value)}catch{return value}})(),
onMyclick: (args) => this.dispatchEvent(new CustomEvent('ex-myclick', {bubbles: true, detail: [args]}))
    } as any
    createRoot(rootEl).render(<Component {...args} />)
    this.shadowRoot!.innerHTML = ''
    this.shadowRoot!.appendChild(rootEl)
    const styleElement = document.createElement('style')
    styleElement.innerHTML = ``
    this.shadowRoot!.appendChild(styleElement)
  }

  static get observedAttributes() { return ["text"]; }
  attributeChangedCallback() {
    this.render()
  }
  constructor() {
    super()
    this.attachShadow({ mode: 'open' })
    this.render()
  }
}

customElements.define('ex-react-demo', ReactWc)