import React from 'react'
          import {createRoot} from 'react-dom'
import Component from './ReactProps'
class ReactWc extends HTMLElement {
  render() {
    const rootEl = document.createElement('div')
    const args = {
      stringprop: (()=>{const value = this.attributes.getNamedItem('stringprop')?.value; try{return JSON.parse(value)}catch{return value}})(),
numprop: (()=>{const value = this.attributes.getNamedItem('numprop')?.value; try{return JSON.parse(value)}catch{return value}})(),
complexprop: (()=>{const value = this.attributes.getNamedItem('complexprop')?.value; try{return JSON.parse(value)}catch{return value}})()
    } as any
    createRoot(rootEl).render(<Component {...args} />)
    this.shadowRoot!.innerHTML = ''
    this.shadowRoot!.appendChild(rootEl)
    const styleElement = document.createElement('style')
    styleElement.innerHTML = ``
    this.shadowRoot!.appendChild(styleElement)
  }

  static get observedAttributes() { return ["stringprop","numprop","complexprop"]; }
  attributeChangedCallback() {
    this.render()
  }
  constructor() {
    super()
    this.attachShadow({ mode: 'open' })
    this.render()
  }
}

customElements.define('ex-react-props', ReactWc)