import React from 'react'
          import {createRoot} from 'react-dom'
import Component from './ReactEmits'
class ReactWc extends HTMLElement {
  render() {
    const rootEl = document.createElement('div')
    const args = {
      onStringevent: (args) => this.dispatchEvent(new CustomEvent('ex-stringevent', {bubbles: true, detail: [args]})),
onNumevent: (args) => this.dispatchEvent(new CustomEvent('ex-numevent', {bubbles: true, detail: [args]})),
onObjevent: (args) => this.dispatchEvent(new CustomEvent('ex-objevent', {bubbles: true, detail: [args]})),
onVoidevent: (args) => this.dispatchEvent(new CustomEvent('ex-voidevent', {bubbles: true, detail: [args]}))
    } as any
    createRoot(rootEl).render(<Component {...args} />)
    this.shadowRoot!.innerHTML = ''
    this.shadowRoot!.appendChild(rootEl)
    const styleElement = document.createElement('style')
    styleElement.innerHTML = ``
    this.shadowRoot!.appendChild(styleElement)
  }

  static get observedAttributes() { return []; }
  attributeChangedCallback() {
    this.render()
  }
  constructor() {
    super()
    this.attachShadow({ mode: 'open' })
    this.render()
  }
}

customElements.define('ex-react-emits', ReactWc)