import React, { useState } from 'react'
import { useWcEvents } from './wcEvents'

export default () => {
  const [count, setCount] = useState(0)
  const emitsRef = useWcEvents({
    'ex-stringevent': (ev) => setCount(count + 1),
  })
  return (
    <div>
      {count}
      <ex-react-emits ref={emitsRef}></ex-react-emits>
      <ex-react-props
        numprop={4}
        stringprop={'str'}
        complexprop={"{ value: 'val' }"}
      ></ex-react-props>
    </div>
  )
}
