import { useEffect, useRef } from 'react'

export const useWcEvents = (events: { [i: string]: (ev: any) => void }) => {
  const ref = useRef<any>()
  useEffect(() => {
    const callback = (ev: any) => events[ev.type](ev.detail)
    Object.keys(events).forEach((key) =>
      ref.current!.addEventListener(key, callback)
    )
    return () => {
      Object.keys(events).forEach((key) =>
        ref.current!.removeEventListener(key, callback)
      )
    }
  })
  return ref
}
